.header-text {
  position: fixed;
  top: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 80px;
  padding: 0 167px;
  padding-top: 40px;

  &__under {
    align-self: center;
    margin: 0 auto;
    max-width: 490px;
    font: 500 20px "PTRoot", sans-serif;
    font-style: normal;
    line-height: 140%;
    letter-spacing: -0.4px;
    text-align: center;
    color: #fff;

    @include small-mobile {
      font-size: 16px;
    }
  }

  &__title {
    align-self: center;
    margin: 0 auto;
    padding-bottom: 32px;
    font-weight: 500;
    font-style: normal;
    font-size: 96px;
    line-height: 100%;
    letter-spacing: 0.96px;
    text-align: center;
    text-transform: uppercase;
    color: #fff;

    @include for-full-hd {
      padding: 40px var(--container-offset-full-hd);
    }

    @include tablet {
      font-size: 69px;
    }

    @include small-tablet {
      font-size: 54px;
    }

    @include small-mobile {
      font-size: 48px;
      line-height: 48px;
      letter-spacing: 1.44px;
    }
  }

  @include small-tablet {
    padding: 40px 110px 0;
    padding: 40px 80px 0;
  }

  @include small-mobile {
    padding: 40px 16px 0;
    font-size: 16px;
  }
}